'use client';
import { useState, useEffect } from 'react';
import { format } from 'date-fns';
import { Calendar, Clock, Edit, Plus, Save, Trash2, X, Loader2, Sparkles } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter } from '@/components/ui/dialog';
import { Card, CardContent } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip';
import type { Event, Role, Volunteer, UniversalRole } from '@/types';
import { useToast } from '@/hooks/use-toast';
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from '@/components/ui/alert-dialog';
import { useMockDatabase } from '@/lib/data';
import { isSameDay, isFuture } from 'date-fns';

interface EventDetailsModalProps {
  event: Event;
  isOpen: boolean;
  onClose: () => void;
  db: ReturnType<typeof useMockDatabase>;
}

const statusColor: Record<Role['status'], string> = {
  Pending: 'bg-accent',
  Confirmed: 'bg-primary',
  Declined: 'bg-destructive',
};

function RoleRow({ role, onUpdate, onDelete, onAssign, volunteers, isSaving }: { role: Role; onUpdate: (roleName: string) => void; onDelete: () => void; onAssign: (volunteerId: string) => void; volunteers: Volunteer[]; isSaving: boolean }) {
  const [isEditing, setIsEditing] = useState(false);
  const [editedName, setEditedName] = useState(role.roleName);

  const handleSave = () => {
    onUpdate(editedName);
    setIsEditing(false);
  };

  return (
    <div className="flex items-center gap-2 p-2 rounded-md hover:bg-muted/50">
      <TooltipProvider>
        <Tooltip>
          <TooltipTrigger>
            <div className={`h-2.5 w-2.5 rounded-full ${statusColor[role.status]}`} />
          </TooltipTrigger>
          <TooltipContent>{role.status}</TooltipContent>
        </Tooltip>
      </TooltipProvider>

      <div className="flex-1">
        {isEditing ? (
          <Input value={editedName} onChange={(e) => setEditedName(e.target.value)} className="h-8" />
        ) : (
          <p className="font-medium">{role.roleName}</p>
        )}
      </div>
      <div className="w-48">
        <Select
          value={role.assignedVolunteerId ?? ''}
          onValueChange={onAssign}
          disabled={isSaving}
        >
          <SelectTrigger className="h-8">
            <SelectValue placeholder="Assign volunteer..." />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="unassign">
              <span className="text-muted-foreground italic">Unassign</span>
            </SelectItem>
            {volunteers.map(v => (
              <SelectItem key={v.id} value={v.id}>{v.name}</SelectItem>
            ))}
          </SelectContent>
        </Select>
      </div>
      <div className="flex items-center gap-1">
        {isEditing ? (
          <Button size="icon" variant="ghost" className="h-8 w-8" onClick={handleSave} disabled={isSaving}>
            <Save className="h-4 w-4" />
          </Button>
        ) : (
          <Button size="icon" variant="ghost" className="h-8 w-8" onClick={() => setIsEditing(true)}>
            <Edit className="h-4 w-4" />
          </Button>
        )}
        <AlertDialog>
          <AlertDialogTrigger asChild>
            <Button size="icon" variant="ghost" className="h-8 w-8 text-destructive hover:text-destructive">
              <Trash2 className="h-4 w-4" />
            </Button>
          </AlertDialogTrigger>
          <AlertDialogContent>
            <AlertDialogHeader>
              <AlertDialogTitle>Are you sure?</AlertDialogTitle>
              <AlertDialogDescription>
                This will permanently delete the "{role.roleName}" role from this service. This action cannot be undone.
              </AlertDialogDescription>
            </AlertDialogHeader>
            <AlertDialogFooter>
              <AlertDialogCancel>Cancel</AlertDialogCancel>
              <AlertDialogAction onClick={onDelete} className="bg-destructive hover:bg-destructive/90 text-destructive-foreground">Delete</AlertDialogAction>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>
      </div>
    </div>
  );
}

export default function EventDetailsModal({ event, isOpen, onClose, db }: EventDetailsModalProps) {
  const { toast } = useToast();
  const [volunteers, setVolunteers] = useState<Volunteer[]>([]);
  const [universalRoles, setUniversalRoles] = useState<UniversalRole[]>([]);
  const [isSaving, setIsSaving] = useState(false);
  const [currentEvent, setCurrentEvent] = useState(event);

  useEffect(() => {
    // Keep internal state updated if the parent's event object changes
    const liveEvent = db.events.find(e => e.id === event.id);
    if (liveEvent) {
      setCurrentEvent(liveEvent);
    }
  }, [db.events, event.id]);


  useEffect(() => {
    if (isOpen) {
      db.getVolunteers().then(setVolunteers);
      db.getUniversalRoles().then(setUniversalRoles);
    }
  }, [isOpen, db]);

  const handleAddRole = async (roleId: string) => {
    const roleToAdd = universalRoles.find(r => r.id === roleId);
    if (!roleToAdd) return;
    
    setIsSaving(true);
    try {
      await db.addRoleToEvent(event.id, {
        roleName: roleToAdd.name,
        assignedVolunteerId: null,
        assignedVolunteerName: null,
        status: 'Pending',
      });
      toast({ title: 'Role Added', description: `"${roleToAdd.name}" has been added.` });
    } catch (e) {
      toast({ variant: 'destructive', title: 'Error', description: 'Failed to add role.' });
    } finally {
      setIsSaving(false);
    }
  };

  const handleUpdateRole = async (roleId: string, roleName: string) => {
    setIsSaving(true);
    try {
      await db.updateRoleInEvent(event.id, roleId, { roleName });
      toast({ title: 'Role Updated', description: `Role name changed to "${roleName}".` });
    } catch (e) {
      toast({ variant: 'destructive', title: 'Error', description: 'Failed to update role.' });
    } finally {
      setIsSaving(false);
    }
  };
  
  const handleDeleteRole = async (roleId: string) => {
    setIsSaving(true);
    try {
      await db.deleteRoleFromEvent(event.id, roleId);
      toast({ title: 'Role Deleted' });
    } catch (e) {
      toast({ variant: 'destructive', title: 'Error', description: 'Failed to delete role.' });
    } finally {
      setIsSaving(false);
    }
  };
  
  const handleAssignVolunteer = async (roleId: string, volunteerId: string) => {
    setIsSaving(true);
    const volunteer = volunteers.find(v => v.id === volunteerId);
    const assignedVolunteerId = volunteerId === 'unassign' ? null : volunteer?.id ?? null;
    const assignedVolunteerName = volunteerId === 'unassign' ? null : volunteer?.name ?? null;

    try {
      await db.updateRoleInEvent(event.id, roleId, { 
        assignedVolunteerId, 
        assignedVolunteerName,
        status: 'Pending' 
      });
      toast({ title: 'Volunteer Assigned', description: `${assignedVolunteerName ?? 'Nobody'} assigned to role.` });
    } catch (e) {
      toast({ variant: 'destructive', title: 'Error', description: 'Failed to assign volunteer.' });
    } finally {
      setIsSaving(false);
    }
  };

  const handleAutoAssign = async () => {
    setIsSaving(true);
    toast({ title: "Auto-assigning...", description: "Finding the best volunteers for the job." });
    
    const unassignedRoles = currentEvent.roles.filter(r => !r.assignedVolunteerId);
    if (unassignedRoles.length === 0) {
        toast({ title: "All roles are already assigned." });
        setIsSaving(false);
        return;
    }

    // 1. Get volunteers already assigned in this specific service to avoid double-booking
    const volunteersAssignedInThisService = new Set(currentEvent.roles
        .map(r => r.assignedVolunteerId)
        .filter((id): id is string => id !== null));

    // 2. Calculate current load for all volunteers based on all upcoming events for fairness
    const allUpcomingEvents = (await db.getEvents(db.MOCK_CHURCH_ID)).filter(e => isFuture(e.eventDate));
    const volunteerAssignmentCounts = new Map<string, number>();
    volunteers.forEach(v => volunteerAssignmentCounts.set(v.id, 0));
    allUpcomingEvents.forEach(e => {
        e.roles.forEach(r => {
            if (r.assignedVolunteerId) {
                const currentCount = volunteerAssignmentCounts.get(r.assignedVolunteerId) || 0;
                volunteerAssignmentCounts.set(r.assignedVolunteerId, currentCount + 1);
            }
        });
    });
    
    let assignmentsMade = 0;

    for (const role of unassignedRoles) {
        let bestCandidate: Volunteer | null = null;
        let highestScore = -1;

        const allVolunteers = await db.getVolunteers();

        for (const volunteer of allVolunteers) {
            // Skip if volunteer is already assigned in this service
            if (volunteersAssignedInThisService.has(volunteer.id)) continue; 
            
            // Check availability
            const availability = await db.getVolunteerAvailability(volunteer.id);
            if (availability.some(unavailableDate => isSameDay(unavailableDate, currentEvent.eventDate))) {
                continue; // Skip if unavailable
            }

            // --- Scoring System ---
            let score = 100; // Start with a base score
            
            // 1. Fairness: Penalize volunteers with more upcoming assignments
            const assignmentCount = volunteerAssignmentCounts.get(volunteer.id) || 0;
            score -= assignmentCount * 20; // Higher penalty for more assignments

            // 2. Role Preference: Reward volunteers who prefer this role
            const universalRole = (await db.getUniversalRoles()).find(ur => ur.name === role.roleName);
            if (universalRole && volunteer.preferences.preferredRoleIds.includes(universalRole.id)) {
                score += 30; // Strong bonus for preferred role
            }

            // 3. Serving Frequency Preference (simplified bonus)
            switch(volunteer.preferences.servingFrequency) {
                case 'weekly': score += 10; break;
                case 'bi-weekly': score += 5; break;
                case 'as-needed': score += 2; break;
            }

            if (score > highestScore) {
                highestScore = score;
                bestCandidate = volunteer;
            }
        }
        
        if (bestCandidate) {
            await handleAssignVolunteer(role.id, bestCandidate.id);
            volunteersAssignedInThisService.add(bestCandidate.id); // Add to set for this run to prevent re-assignment
            // Increment their assignment count for the next iteration in this loop
            const currentCount = volunteerAssignmentCounts.get(bestCandidate.id) || 0;
            volunteerAssignmentCounts.set(bestCandidate.id, currentCount + 1);
            assignmentsMade++;
        }
    }
    
    toast({
        title: "Auto-Assign Complete",
        description: `Successfully made ${assignmentsMade} new assignments.`
    });
    setIsSaving(false);
  }

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-3xl">
        <DialogHeader>
          <DialogTitle className="font-headline text-2xl">{currentEvent.eventName}</DialogTitle>
          <div className="flex items-center gap-4 text-sm text-muted-foreground pt-2">
            <span className="flex items-center gap-1.5"><Calendar className="h-4 w-4" /> {format(currentEvent.eventDate, 'EEEE, MMMM d, yyyy')}</span>
            <span className="flex items-center gap-1.5"><Clock className="h-4 w-4" /> {format(currentEvent.eventDate, 'p')}</span>
          </div>
        </DialogHeader>
        
        <div className="grid gap-4 py-4 max-h-[70vh] overflow-y-auto pr-2">
            {currentEvent.notes && (
                <Card className="bg-muted/50">
                    <CardContent className="p-4">
                        <p className="text-sm text-foreground">{currentEvent.notes}</p>
                    </CardContent>
                </Card>
            )}

            <Card>
                <CardContent className="p-4">
                     <div className="flex justify-between items-center mb-4">
                         <h4 className="font-semibold">Volunteer Roles</h4>
                     </div>
                    <div className="space-y-1">
                        {currentEvent.roles.map(role => (
                            <RoleRow 
                                key={role.id}
                                role={role}
                                volunteers={volunteers}
                                isSaving={isSaving}
                                onUpdate={(roleName) => handleUpdateRole(role.id, roleName)}
                                onDelete={() => handleDeleteRole(role.id)}
                                onAssign={(volunteerId) => handleAssignVolunteer(role.id, volunteerId)}
                            />
                        ))}
                         {currentEvent.roles.length === 0 && (
                             <p className="text-sm text-center text-muted-foreground py-4">No roles for this service yet.</p>
                         )}
                    </div>
                    <div className="mt-4 pt-4 border-t space-y-4">
                        <Select onValueChange={handleAddRole} value="">
                            <SelectTrigger>
                                <SelectValue placeholder="Add a role from your list..." />
                            </SelectTrigger>
                            <SelectContent>
                                {universalRoles.map(role => (
                                    <SelectItem key={role.id} value={role.id}>{role.name}</SelectItem>
                                ))}
                            </SelectContent>
                        </Select>
                        <Button variant="outline" className="w-full" onClick={handleAutoAssign} disabled={isSaving}>
                             {isSaving ? <Loader2 className="mr-2 h-4 w-4 animate-spin"/> : <Sparkles className="mr-2 h-4 w-4" />}
                             Auto-Assign Unfilled Roles
                         </Button>
                    </div>
                </CardContent>
            </Card>
        </div>
        <DialogFooter>
          <Button onClick={onClose} variant="outline">Close</Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
