
'use client';
import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Plus, Upload, Edit, User, Clock } from 'lucide-react';
import { useMockDatabase } from '@/lib/data';
import EventCalendar from '@/components/admin/EventCalendar';
import CreateEventModal from '@/components/admin/CreateEventModal';
import CreateTemplateModal from '@/components/admin/CreateTemplateModal';
import EventDetailsModal from '@/components/admin/EventDetailsModal';
import { Header } from '@/components/layout/Header';
import type { Event } from '@/types';
import ImportIcsModal from '@/components/admin/ImportIcsModal';
import { parseIcsContent } from '@/ai/flows/parse-ics-flow';
import { useAuth } from '@/hooks/use-auth';
import { useRouter } from 'next/navigation';
import AppShell from '@/components/layout/AppShell';
import { format, isSameDay } from 'date-fns';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';

export default function ServicesPage() {
  const db = useMockDatabase();
  const [isCreateEventModalOpen, setCreateEventModalOpen] = useState(false);
  const [isCreateTemplateModalOpen, setCreateTemplateModalOpen] = useState(false);
  const [isImportModalOpen, setImportModalOpen] = useState(false);
  const [selectedDay, setSelectedDay] = useState<Date | null>(null);
  const [selectedDayEvents, setSelectedDayEvents] = useState<Event[]>([]);
  const [eventToEdit, setEventToEdit] = useState<Event | null>(null);
  const { isAdmin, isLoggedIn } = useAuth();
  const router = useRouter();
  
  // A simple way to force re-render when DB changes
  const [dbVersion, setDbVersion] = useState(0);
  useEffect(() => {
    const interval = setInterval(() => {
        setDbVersion(v => v + 1);
    }, 1000);
    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    if (!isLoggedIn) {
      router.push('/');
    } else if (!isAdmin) {
       router.push('/my-schedule');
    }
  }, [isLoggedIn, isAdmin, router]);

  const handleDaySelect = (day: Date) => {
    setSelectedDay(day);
    const events = db.events.filter(e => e.churchId === db.MOCK_CHURCH_ID && isSameDay(e.eventDate, day))
      .sort((a,b) => a.eventDate.getTime() - b.eventDate.getTime());
    setSelectedDayEvents(events);
  };
  
  const handleEditModalClose = () => {
    setEventToEdit(null)
  }

  const handleCreateEventModalClose = () => {
    setCreateEventModalOpen(false)
  }
  
  const handleCreateTemplateModalClose = () => {
    setCreateTemplateModalOpen(false);
  }

  const handleOpenCreateTemplateModal = () => {
    setCreateEventModalOpen(false);
    setCreateTemplateModalOpen(true);
  }

  const handleImportModalClose = () => {
    setImportModalOpen(false);
  }
  
  if (!isLoggedIn || !isAdmin) {
    return null; // or a loading spinner while redirecting
  }

  return (
    <AppShell>
      <div className="flex min-h-screen w-full flex-col bg-muted/40">
        <Header />
        <main className="flex flex-1 flex-col gap-4 p-4 md:gap-8 md:p-8">
          <div className="flex items-center justify-between flex-wrap gap-4">
            <div>
              <h1 className="text-3xl font-bold font-headline tracking-tight">
                Service Management
              </h1>
              <p className="text-muted-foreground mt-1">
                Manage, create, and import church services. Click a day to view its events.
              </p>
            </div>
            <div className="flex items-center gap-2">
                <Button variant="outline" size="sm" onClick={() => setImportModalOpen(true)}>
                    <Upload className="mr-2 h-4 w-4" /> Import
                </Button>
                <Button size="sm" onClick={() => setCreateEventModalOpen(true)}>
                    <Plus className="mr-2 h-4 w-4" /> Create Service
                </Button>
            </div>
          </div>

          <EventCalendar 
              events={db.events.filter(e => e.churchId === db.MOCK_CHURCH_ID)} 
              onDayClick={handleDaySelect} 
              selectedDay={selectedDay}
              db={db}
          />
          
          {selectedDayEvents.length > 0 && (
             <div className="animate-fade-in">
                <h3 className="text-lg font-semibold mb-2 font-headline">
                    Events for {format(selectedDayEvents[0].eventDate, 'EEEE, MMMM d, yyyy')}
                </h3>
                <Accordion type="single" collapsible className="w-full" defaultValue={selectedDayEvents.length === 1 ? selectedDayEvents[0].id : undefined}>
                    {selectedDayEvents.map(event => (
                        <AccordionItem value={event.id} key={event.id} className="bg-card border-b-0 rounded-lg shadow-sm mb-2">
                           <div className="flex items-center justify-between p-4 rounded-t-lg">
                               <AccordionTrigger className="p-0 hover:no-underline flex-1">
                                    <div className="text-left">
                                        <p className="font-headline text-lg">{event.eventName}</p>
                                        <p className="text-sm text-muted-foreground font-normal">{format(event.eventDate, 'p')}</p>
                                    </div>
                                </AccordionTrigger>
                                <Button variant="outline" size="sm" onClick={() => setEventToEdit(event)} className="ml-4">
                                    <Edit className="mr-2 h-4 w-4" /> Edit
                                </Button>
                            </div>
                           <AccordionContent className="p-4 pt-0">
                                <h4 className="font-semibold mb-2 text-sm">Volunteer Roles</h4>
                                {event.roles.length > 0 ? (
                                <div className="grid gap-2 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4">
                                    {event.roles.map(role => (
                                        <div key={role.id} className="flex items-center gap-3 p-3 rounded-md border bg-muted/50">
                                            <User className="h-5 w-5 text-primary" />
                                            <div>
                                                <p className="font-semibold">{role.roleName}</p>
                                                <p className="text-sm text-muted-foreground">{role.assignedVolunteerName || '(Unassigned)'}</p>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                                ) : (
                                <p className="text-sm text-muted-foreground text-center py-4 border-2 border-dashed rounded-md">No roles assigned for this service.</p>
                                )}
                           </AccordionContent>
                        </AccordionItem>
                    ))}
                </Accordion>
             </div>
          )}

        </main>
        <CreateEventModal
          isOpen={isCreateEventModalOpen}
          onClose={handleCreateEventModalClose}
          db={db}
          onCreateTemplate={handleOpenCreateTemplateModal}
        />
        <CreateTemplateModal
          isOpen={isCreateTemplateModalOpen}
          onClose={handleCreateTemplateModalClose}
          db={db}
        />
        <ImportIcsModal
          isOpen={isImportModalOpen}
          onClose={handleImportModalClose}
          importAction={parseIcsContent}
          createEvent={db.createEvent}
        />
        {eventToEdit && (
          <EventDetailsModal
            key={eventToEdit.id}
            event={eventToEdit}
            isOpen={!!eventToEdit}
            onClose={handleEditModalClose}
            db={db}
          />
        )}
      </div>
    </AppShell>
  );
}
